.wrapper {
  position: relative;
  text-align: center;
  padding-bottom: 50px;
  height: 640px;
}

.navBar {
  position: relative;
  z-index: 99999;
  margin-bottom: 30px;
}

.img {
  display: block;
  height: 69px;
  width: auto;
  user-select: none;
}

.navBarNotTransparent {
  position: relative;
  z-index: 99999;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 20px 0 20px;
}

.mediaMenu {
  display: flex;
  align-items: center;
  color: #ffffff;
  font-size: 24px;
  font-family: "Poiret One";
}

.mediaMenu .span {
  margin-right: 25px;
  user-select: none;
}

.header {
  font-size: 30px;
  line-height: 48px;
  text-transform: uppercase;
  font-family: 'Poiret One';
  text-align: center;
  color: #69C9CA;
  margin-bottom: 15px;
}

.text {
  width: 320px;
  margin: 0 auto;
  text-align: left;
  font-weight: 300;
  font-size: 14px;
  line-height: 24px;
  color: #FFFFFF;
  margin-bottom: 15px;
}
.imgPortal {
  display: block;
  margin: 0 auto;
  width: 320px;
  height: auto;
}

@media(min-width: 1200px) {
  .wrapper {
    height: 100vh;
    padding-bottom: 0;
    background-image: url(../../../img/background/pagesLeftFirstColor.svg), url(../../../img/background/pagesRightFirstWhite.svg);
    background-repeat: no-repeat;
    background-size: auto 50%, auto 38%;
    background-position: 0% 55%, 100% 100%;
  }
  .img {
    height: 106px;
  }

  .navBar {
    margin-bottom: 20px;
  }

  .navBarNotTransparent {
    padding: 40px 40px 0 100px;
  }

  .header {
    font-size: 60px;
    line-height: 95px;
    position: relative;
    display: inline-block;
    margin-bottom: 23px;
    margin-top: 0;
  }

  .buttons {
    position: absolute;
    display: block;
    width: 197px;
    left: -110px;
    top: 50%;
    transform: translate(0, -50%);
  }
  .text {
    width: auto;
    text-align-last: center;
    margin: 0 0 30px 0;
    font-size: 18px;
    line-height: 30px;
  }
  .imgPortal {
    width: auto;
    height: calc(100vh - 474px);
  }
}
